import React, { useRef, useState, useEffect, lazy, Suspense } from 'react'
import { Route } from "react-router-dom";
import { Segment,  Dimmer, Loader } from 'semantic-ui-react';

// import HeaderSui from './layouts/HeaderSui';
// import ModalImageViewer from './components/Modal';
// import ScrollTopArrow from './components/ScrollTopArrow'
// import HardsSkillsSUI from './layouts/HardSkillsSUI';
// import FooterSui from './layouts/FooterSUI';
// import ProjectsSui from './layouts/ProjectsSui';
// import ContactSUI from './layouts/ContactSUI';
import VideoHeader from './layouts/VideoHeader';


const HeaderSui = lazy(() => import('./layouts/HeaderSui'));
const ModalImageViewer = lazy(() => import('./components/Modal'));
const ScrollTopArrow = lazy(() => import('./components/ScrollTopArrow'));
const HardsSkillsSUI = lazy(() => import('./layouts/HardSkillsSUI'));
const FooterSui = lazy(() => import('./layouts/FooterSUI'));
const ProjectsSui = lazy(() => import('./layouts/ProjectsSui'));
const ContactSUI = lazy(() => import('./layouts/ContactSUI'));
// const VideoHeader = lazy(() => import('./layouts/VideoHeader'));



const App = () =>  {
  

    const mainRef = useRef();
    const projectsRef = useRef();
    const contactRef = useRef();

    const ScrolltoY = (y) => {
      window.scrollTo({
        top: y,
        left: 0,
        behavior: "smooth"
      });
    }

    const executeScroll = (ref) => {
      if (ref === 'main') {ScrolltoY(mainRef.current.offsetTop-100)}
      else if(ref ==='projects') { ScrolltoY(projectsRef.current.offsetTop-105)}
      else if(ref ==='contact') { ScrolltoY(contactRef.current.offsetTop-50)}
      else {}
    }

    const [active, setActive] = useState('main');

  useEffect(() => {
    switch (active) {
         case 'main': 
             return executeScroll('main')
         case 'projects': 
             return executeScroll('projects')
         case 'contact': 
             return executeScroll('contact')
         default:
             return;
         }
     }, [active]);


    return (
      <Suspense fallback={
        <Segment className='loading-background'>
        <Dimmer active>
          <Loader size='massive'>Loading</Loader>
        </Dimmer>
        </Segment>
      }>
        <Segment className='background'> 
          <HeaderSui />
          
          <Route exact path='/' render={() => {setActive('main')}} />
          <Route exact path='/projects' render={() => {setActive('projects')}} />
          <Route exact path='/contact' render={() => {setActive('contact')}} />

          <div ref={mainRef}>
          <VideoHeader skillsActive={true}/>
          <HardsSkillsSUI />
          </div>

          <div ref={projectsRef}>
          <ProjectsSui/>
          </div>

          <div ref={contactRef}>
          <ContactSUI/>
          </div>

          <ModalImageViewer />
          <ScrollTopArrow />
          <FooterSui/>
        </Segment>
    
        
            </Suspense>
      );
  };

export default App;