import React from 'react'
import SkillsSui from './SkillsSUI';
import vid_header from '../vid/vid_header.mp4';
import VKM_Logo from '../img/VKM_logo_v5.png';

const VideoHeader = () => {

    return (
        <>
        <div className="header-video">
            <div className="bg-video">
                <video className='bg-video__content' autoPlay loop={false} muted>
                    <source src={vid_header} type='video/mp4' />
                </video>
            </div>

            <div className="header-video__logo-box">
                <img src={VKM_Logo} alt="logo" />
            </div>

            <div className="header-video__text-box">
            <h1 className="heading-primary">
                <span className="heading-primary--main">Krzysztof Meyer</span>
                <span className="heading-primary--sub">Automation and robotics engineer &amp; Web developer</span>
            </h1>
            </div>
            
            <div className="header-video__info-box">
                <SkillsSui />
            </div>
        </div>
        </>
    )
}

export default VideoHeader
