import React, { useContext } from 'react';
import { StoreContext } from '../Store/StoreProvider';
import { Grid, Segment, Icon, List, Header, Divider, Card } from 'semantic-ui-react';

import ListItems from '../components/ListItems'

import { InlineIcon as IconifyInlineIcon } from '@iconify/react';
import mongodbIcon from '@iconify-icons/cib/mongodb';
import expressIcon from '@iconify-icons/simple-icons/express';
import nodejsIcon from '@iconify-icons/logos/nodejs';
import reactIcon from '@iconify-icons/logos/react';
import reduxIcon from '@iconify-icons/logos/redux';
import reactRouter from '@iconify-icons/logos/react-router';
import sassIcon from '@iconify-icons/logos/sass';
import nodeRed from '@iconify-icons/cib/node-red';
import html5Icon from '@iconify-icons/cib/html5';
import css3Shiled from '@iconify-icons/cib/css3-shiled';
import javascriptIcon from '@iconify-icons/logos/javascript';
import vueIcon from '@iconify-icons/logos/vue';


const SkillsSui = () => {

    const  { aboutMe } = useContext(StoreContext);

    const iconifyIconsList = [
      {name: reactIcon,      header: "React.js",  color:'',  className:'technologies-grid--3', key:'3' },
      {name: reactRouter,    header: "React-Router",  color:'',  className:'technologies-grid--7', key:'7' },
      {name: reduxIcon,      header: "Redux",  color:'',  className:'technologies-grid--6', key:'6' },
      {name: javascriptIcon, header: "JavaScript",  color:'',  className:'technologies-grid--8', key:'8' },
      {name: html5Icon,      header: "HTML5",  color:'#e34c26',  className:'technologies-grid--9', key:'9' },
      {name: css3Shiled,     header: "CCS3",  color:'#264de4',  className:'technologies-grid--10', key:'10' },
      {name: sassIcon,       header: "Sass",  color:'',  className:'technologies-grid--11', key:'11' },
      {name: mongodbIcon,    header: "MongoDB",  color:'green',  className:'technologies-grid--1', key:'1'},
      {name: expressIcon,    header: "Express",  color:'',  className:'technologies-grid--2', key:'2' },
      {name: nodejsIcon,     header: "Node.js",  color:'',  className:'technologies-grid--4', key:'4' },
      {name: nodeRed,        header: "Node-Red",  color:'red',  className:'technologies-grid--5', key:'5' },
      {name: vueIcon,        header: "Vue.js",  color:'',  className:'technologies-grid--12', key:'12' },
    ]

    const iconifyHeight = '7.5vh'

    const skillsContent = (
      <>
        <Grid inverted stackable padded centered vertical >

        <Grid.Row className='skills'>
            <Grid.Column widescreen={13}>
              <Grid.Row divided>
                <Header inverted as='h2'>
                  <Icon name={aboutMe.headerIcon} />
                <Header.Content>
                  {aboutMe.headerContent}
                </Header.Content>
                </Header> 
              </Grid.Row>
              <Grid.Row className='u-margin-bottom-big'>
                <Divider horizontal inverted>Short Briefing of my work-life</Divider>
                <Segment vertical >
                    <List animated celled selection size="big" floated="right" className='headerListInfoBg u-margin-bottom-small'> 
                      <ListItems ListItemsProps={aboutMe} />
                    </List>
                      </Segment>

                         <Divider inverted horizontal className='u-margin-top-small u-margin-bottom-small'>Technologies &amp; programming languages</Divider>
                <Card.Group itemsPerRow={6} centered stackable >
                {iconifyIconsList.map((icon) => 
                <Card className='technologies-grid'> 
                  <Card.Content>
                  <div className="iconifyIcon">
                    <IconifyInlineIcon 
                    key={icon.key} 
                    icon={icon.name} 
                    height={iconifyHeight} 
                    color={icon.color} />
                  </div>
                  </Card.Content>
                </Card> )}
                </Card.Group>
                </Grid.Row>

             

            </Grid.Column>
          </Grid.Row>

      </Grid>
      </>
      )


    return (
<>

<Segment vertical> 
{skillsContent}
</Segment> 

</>
     );
}
 
export default SkillsSui;